import { HttpClient } from '@angular/common/http';
import { Component, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['../../assets/css/FundBazarLogin.css']
})
export class HomeComponent {

    viewdata: any;
    submitted: false;
    @ViewChild('frmLogin', { static: true }) frmLogin;

    show: boolean = false;
    buttonName: string;
    constructor(private http: HttpClient, private tost: ToastrService) { }
    UserEmail;
    UserPassword;
    msg;

    // This For Validation Functionality
    toggle() {
        if (this.UserEmail == null || this.UserEmail == '') {
            this.buttonName = "Please enter email";
            this.tost.error(this.buttonName, "title", {
                positionClass: 'toast-top-center',
                timeOut:3000,
            });

           /* this.show = !this.show;*/
        }
        else if (this.UserPassword == null || this.UserPassword == '') {
            this.buttonName = "Please enter password";
           /* this.show = !this.show;*/
        }
    }

    onTextMouseEnter() {
        this.show = true;
    }

    onTextMouseLeave() {
        window.setTimeout(function () {
            this.show = false
        }.bind(this), 3000);
    }

    //This For Login Functionality 
    LoginBtn(frmLogin: NgForm) {
        this.frmLogin.submitted = true;
        if (frmLogin.valid) {
            let param = {
                "UserName": this.UserEmail,
                "UserPassword": this.UserPassword,
            }
            this.http.post<any>(`Home/LoginData`, { "JsonData": JSON.stringify(param) }).pipe().subscribe(data => {
                data = JSON.parse(data.data);
                if (data[0].MSG == "1") {
                    
                    alert("Login Successfully");
                }
                else {
                    this.show = !this.show;
                    this.buttonName = "Invalid email or password";
                }
            }, error => { });
        }
        else {
            return false;
        }
    }
}
