import { HttpClient } from '@angular/common/http';
import { Component, ElementRef, Inject, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { environment } from '../../environments/environment';

@Component({
    selector: 'app-registration',
    templateUrl: './registration.component.html'
})
export class RegistrationComponent {

    NamePattern;
    MobilePattern;
    EmailPattern;
    disabledbox = false;
    dismobotpcntrl = false;
    disbox = false;
    dispassinput = false;
    disnext = false;
    disableName = false;
    disableEmail = false;
    disboxverify = false;
    submitted = false;
    showEmail = true;
    hideEmail = false;
    showMobile = true;
    hideMobile = false;
    sdata: any;
    testurl: string;
    @ViewChild('frmRegister', { static: true }) frmRegister;
    constructor(private http: HttpClient, @Inject('BASE_URL') _baseUrl: string) {
        this.testurl = _baseUrl;
    }
    name;
    email;
    mobile;
    pass;
    emailotp;
    mobileotp;
    confirmPassword;

    toEnable(frmRegister: NgForm) {
        this.frmRegister.submitted = true;
        if (this.frmRegister.valid) {

            let param = {
                "cemail": this.email
            }

            this.http.post<any>(`Registration/checkEmail`, { "JsonData": JSON.stringify(param) }).pipe().subscribe(data => {
                data = JSON.parse(data.data);
                if (data[0].MSG == "1") {
                    alert("Email already Exist...!!");
                }
                else {

                    this.showEmail = !this.showEmail;
                    this.hideEmail = !this.hideEmail;
                    this.disableName = !this.disableName;
                    this.disableEmail = !this.disableEmail;
                    this.disabledbox = !this.disabledbox;
                    alert("OTP Send Successfully...!!");

                }

            }, error => { });
        }
        else {
            return false;
        }
    }
    toShowField() {
        this.disableName = !this.disableName;
        this.disableEmail = !this.disableEmail;
        this.showEmail = !this.showEmail;
        this.hideEmail = !this.hideEmail;
        this.disabledbox = !this.disabledbox;

    }

    toEnablemobile() {
        var otpMlen = String(this.emailotp)

        if (otpMlen.length == 6) {

            if (this.emailotp == null || this.emailotp == '') {
                this.disbox = this.disbox;

            }
            else {

                this.disbox = !this.disbox;
                this.disboxverify = !this.disboxverify;
            }
        }

    }
    toEnablemobverifier(frmRegister: NgForm) {
        this.frmRegister.submitted = true
        if (this.frmRegister.valid) {

            let param = {
                "cmobile": this.mobile
            }

            this.http.post<any>(`Registration/checkMobile`, { "JsonData": JSON.stringify(param) }).pipe().subscribe(data => {
                data = JSON.parse(data.data);
                if (data[0].MSG == "1") {
                    alert("Mobile Number already Exist...!!");
                }
                else {
                    this.showMobile = !this.showMobile;
                    this.hideMobile = !this.hideMobile;
                    this.disbox = !this.disbox;

                    this.dismobotpcntrl = !this.dismobotpcntrl;
                    alert("OTP Send Successfully...!!");

                }

            }, error => { });
        }
        else {
            return false;
        }
    }
    toShowFieldMobile() {
        this.showMobile = !this.showMobile;
        this.hideMobile = !this.hideMobile;
        this.disbox = !this.disbox;
        this.dismobotpcntrl = !this.dismobotpcntrl;
    }
    toEnablepassinput() {
        var otpElen = String(this.mobileotp)

        if (otpElen.length == 6) {

            if (this.mobileotp == null || this.mobileotp == '') {
                this.dispassinput = this.dispassinput;

            }
            else {

                this.dispassinput = !this.dispassinput;
                this.disnext = !this.disnext;
            }
        }
    }
    EnableNext() {
        if (this.pass == this.confirmPassword) {
            this.disnext = this.disnext;
        }
        else {

            this.disnext = !this.disnext;
        }
    }
    submitdata(frmRegister: NgForm) {

        this.frmRegister.submitted = true;
        if (this.pass !== this.confirmPassword) {
            this.frmRegister.form.setErrors({ invalid: true });
        }

        if (this.frmRegister.valid) {
            let param = {
                "cname": this.name, "cemail": this.email, "cmobile": this.mobile, "cpass": this.pass
            }

            this.http.post<any>(`Registration/submitData`, { "JsonData": JSON.stringify(param) }).pipe().subscribe(data => {
                data = JSON.parse(data.data);

                this.sdata = data;
                alert("Data Submitted Successfully...!!");


                frmRegister.resetForm();
                window.location.reload();
                //this.disableName = !this.disableName;
                //this.disableEmail = !this.disableEmail;
                //this.hideEmail = !this.hideEmail;
                //this.showEmail = !this.showEmail;
                //this.showMobile = !this.showMobile;
                //this.hideMobile = !this.hideMobile;
                //this.dispassinput = !this.dispassinput;
                //this.disnext = !this.disnext;

            }, error => { });
        }
        else {
            return false;
        }
    }

    ngOnInit() {

        this.NamePattern = environment.NamePattern;
        this.MobilePattern = environment.MobilePattern;
        this.EmailPattern = environment.EmailPattern;



    }
}
